.blog__elem{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	background:-webkit-gradient(linear,left top, left bottom,from(rgba(20,20,20,0.8)),to(rgba(30,30,30,0.8)));
	background:-webkit-linear-gradient(rgba(20,20,20,0.8),rgba(30,30,30,0.8));
	background:-o-linear-gradient(rgba(20,20,20,0.8),rgba(30,30,30,0.8));
	background:linear-gradient(rgba(20,20,20,0.8),rgba(30,30,30,0.8));
	border:25px dotted #999;
	border-bottom: none;
	height: 100%
}


.block{
	width:50%;
}

@media screen and (min-width: 1201px) and (max-width: 2000px){
	.blog{
		width: 50%;
	}
	.blog-size-full{
		height: 50%;
	}
}

@media screen and (min-width: 901px) and (max-width: 1200px){
	.blog{
		width:70%;
	}
	.blog-size-full{
		height: 40%;
	}
}
@media screen and (min-width: 480px) and (max-width: 900px){
	.blog{
		width:80%;
	}
	.blog-size-full{
		height: 33%;
	}
}

@media screen and (min-width: 100px) and (max-width: 479px){
	.blog{
		width:100%;
	}
	.blog-size-full{
		height: 25%;
	}
}




.blog-elem-size-full{
	height:100%;
	width:100%;
}

.center {
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.column{
	-ms-flex-direction: column;
	    flex-direction: column;
}

.row{
	-ms-flex-direction: row;
	    flex-direction: row
}

.fix{
	position: fixed;
	right:0;
	height:100%;
}



.uupload {
	color:#FFF;
	background: -webkit-gradient(linear,left top, left bottom,from(#333),to(#567));
	background: -webkit-linear-gradient(#333,#567);
	background: -o-linear-gradient(#333,#567);
	background: linear-gradient(#333,#567);
	width:15vW;
	border-top-left-radius: 50%;
	border-bottom-left-radius: 50%;
	border:none;
	-webkit-transform: translate(13vw,0);
	    -ms-transform: translate(13vw,0);
	        transform: translate(13vw,0);
}

.uupload:hover {
	width:6vw;
	-webkit-transition: all 0.4s ease;
	-o-transition: all 0.4s ease;
	transition: all 0.4s ease;
	-webkit-transform: translate(0,0);
	    -ms-transform: translate(0,0);
	        transform: translate(0,0);
}



