.card {
	height:80%;
	width:60%;
	border: 2px solid #FFF;
	border-radius: 4%;
}


.center{
	display:-ms-flexbox;
	display:flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}


.like {
	height:40%;
	width:40%;
	opacity: 0.35;
	cursor: pointer;
	-o-object-fit: cover;
	   object-fit: cover;
}

@-webkit-keyframes pulse{
	0%{
		height:40%;
		width:40%;
		opacity: 0.35;
		font-size: 1pt;
		color:#F00;
	}
	40%{
		height:80%;
		width:80%;
		opacity: 1.0;
		font-size: 3em;
	}
	100%{
		height:40%;
		width:40%;
		font-size: 1pt;
		opacity: 0.35;
	}
}

@keyframes pulse{
	0%{
		height:40%;
		width:40%;
		opacity: 0.35;
		font-size: 1pt;
		color:#F00;
	}
	40%{
		height:80%;
		width:80%;
		opacity: 1.0;
		font-size: 3em;
	}
	100%{
		height:40%;
		width:40%;
		font-size: 1pt;
		opacity: 0.35;
	}
}

.likes__info{
	position: absolute;
	right: 5%;
	bottom: 0;
	font-size: 1.5em !important; 
	color: #000;
}

.like__count{
	text-decoration: none;
	text-transform: none;
	color: #000;
}
.like-animate{
	-webkit-animation-duration: 0.8s;
	        animation-duration: 0.8s;
	-webkit-animation-name:pulse;
	        animation-name:pulse;
}


